<template>
	<view>
		<u-cell-group>
			<u-cell title="文件名" :value="fileDetails.fileName"></u-cell>
			<u-cell title="文件类型" :value="fileDetails.fileType"></u-cell>
			<!-- <u-cell title="文件地址" :value="fileDetails.fileUrl"></u-cell> -->
			<u-cell title="文件备注" :value="fileDetails.remark"></u-cell>
			<u-cell title="创建时间" :value="fileDetails.createTime"></u-cell>
			<u-cell title="更新时间" :value="fileDetails.updateTime"></u-cell>
		</u-cell-group>
		<view class="flexb btns">
			<u-button type="primary" text="打开文件" @click="open"></u-button>
			<u-button type="success" text="评论" @click="comment"></u-button>
		</view>
		<u-popup :show="showCommentPopup" @close="showCommentPopup = false">
			<view class="flexb" style="gap: 12rpx; padding: 24rpx;box-sizing: border-box;">
				<u--textarea style="flex: 1;" v-model="commentVal" placeholder="请输入内容" autoHeight></u--textarea>
				<view style="width: 60rpx;margin-right: 24rpx;">
					<u-button text="发送"  size="mini" color="linear-gradient(to right, rgb(255, 193, 46), rgb(247, 238, 138))"></u-button>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				fileDetails: {},
				showCommentPopup: false,
				commentVal: ''
			};
		},
		onLoad({
			params
		}) {
			this.fileDetails = JSON.parse(params)
		},
		methods: {
			comment() {
				this.showCommentPopup = true
			},
			open() {
				uni.downloadFile({
					url: this.fileDetails,
					success: function(res) {
						var filePath = res.tempFilePath.fileUrl;
						uni.openDocument({
							filePath: filePath,
							showMenu: true,
							success: function(res) {}
						});
					},
					fail(e) {
						uni.showToast({
							title: e.errMsg,
							icon: 'none'
						})
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.btns {
		width: 100%;
		position: absolute;
		bottom: 0rpx;
		left: 0px;
		padding: 0px 24rpx;
		box-sizing: border-box;
		gap: 24rpx;
		padding-bottom: constant(safe-area-inset-bottom);
		padding-bottom: env(safe-area-inset-bottom);
	}
</style>